import React, { useState, useEffect } from 'react'

import * as Types from './page-menu.types'
import * as Styles from './page-menu.styles'

import kebabCase from 'lodash.kebabcase'

// Prevent widows
const formatTitle = (title: string): string => {
  return title.replace(/\s(?=[^\s]*$)/g, '\u00A0')
}

const SubmenuMdx: React.FunctionComponent<Types.PageSubmenuProps> = ({ submenu }): JSX.Element => (
  <Styles.List level={1}>
    {submenu.map(({ id, title }, index: number) => (
      <Styles.SecondLevelEntry key={index}>
        <Styles.SecondLevelLink href={`#${id}`}>
          <Styles.Name>{formatTitle(title)}</Styles.Name>
        </Styles.SecondLevelLink>
      </Styles.SecondLevelEntry>
    )
  )}
  </Styles.List>
)

const PageMenu: React.FunctionComponent<Types.PageMenuProps> = ({ data }): JSX.Element => {
  const [submenusMdx, setSubmenusMdx] = useState([])
  const contentInSubdirectory = new RegExp(/^\/docs\/([\w-_]+\/)[\w-_]+\/?$/)

  if (!data || !data.pageContent || !data.pageContent.edges) {
    return null
  }

  const firstLevelEntries = data.pageContent.edges
    .map(({ node }) => node.fields)
    .filter((node: any) => contentInSubdirectory.test(node.slug))

  // useEffect mdxTitles
  useEffect(() => {
    const mdxTitles: HTMLElement[] = Array.from(document.querySelectorAll('.page .mdx-subtitle'))

    const mdxTitlesDomElements = mdxTitles
      .map(mdxTitle => ({
          'wrapper': mdxTitle.closest('.page').id,
          'title': mdxTitle.textContent,
          'id': mdxTitle.id
        })
      )
      // remove entries with missing attribute
      .filter((entry: any) => {
        return Object.values(entry).every(value => (value !== null))
      })

    const mappedMdxTitles = mdxTitlesDomElements.reduce((total, entry) => {
      const entryNew = {
        id: entry.id,
        title: entry.title
      }

      if (!(entry.wrapper in total)) {
        total[entry.wrapper] = [entryNew]

        return total
      }

      total[entry.wrapper] = total[entry.wrapper].concat([entryNew])

      return total
    }, {} as any)

    setSubmenusMdx(mappedMdxTitles)
  }, [])

  if (!firstLevelEntries.length) {
    return null
  }

  return (
    <Styles.Container id='pageMenu'>
      <Styles.Title>
        On this page
      </Styles.Title>
      <Styles.List level={0}>
        {firstLevelEntries.map(({ title }: any, index: number) => {
          const titleConverted = kebabCase(title.toLowerCase())

          const target = `#${titleConverted}`
          const submenuMdx = submenusMdx[titleConverted] || []

          return (
            <Styles.Entry key={index}>
              <Styles.Link href={target}>
                {title}
              </Styles.Link>
              {!!submenuMdx.length && <SubmenuMdx target={target} submenu={submenuMdx} />}
            </Styles.Entry>
          )
        }
        )}
      </Styles.List>
    </Styles.Container>
  )
}

export default PageMenu
